<template>
  <div id="FindTheNumber" class="pb-16">
    
    <Alert ref="Alert"/>

    
    <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :score="score" :maxW="'max-w-xl'" ref="InteractiveBord"/>

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3 px-5 text-2xl">

        <div class="h-full w-full py-12 rounded-lg overflow-hidden border bg-purple-400" v-text="question" />
        <br>
        <div class="h-full w-full p-1 rounded-lg overflow-hidden border bg-purple-400" v-text="userNumber?userNumber:'.'"/>
        <br>
        <div class="w-full flex flex-wrap">
          
            <div class="w-4/5 flex flex-wrap">
              <div class="w-1/3 flex flex-wrap" v-for="(number, index) in 9" :key="index">
                <div class="w-full py-3 m-1 rounded-lg overflow-hidden cursor-pointer border bg-purple-500" v-text="number" @click="clickNumber(number)"/>
              </div>
            </div>
            <div class="w-1/5 flex flex-col">
              <div class="w-full py-8 m-1 rounded-lg overflow-hidden cursor-pointer border bg-purple-400" v-text="'clear'" @click="clickClear()"/>
              <div class="w-full py-8 m-1 rounded-lg overflow-hidden cursor-pointer border bg-purple-500" v-text="'0'" @click="clickNumber(0)"/>
            </div>

        </div>
      </div>
    </div>
    
    <LevelsSelect @dataForApp="updateLevelValue" />
    
  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'FindTheNumber',
  components: {
    Alert,
    InteractiveBord,
    LevelsSelect
  },
  data() {
    return {
      question:"20 - 6",
      answer:550,
      mathEx:["+","-","*","/"],

      userNumber:0,

      score:0,

      stopGame:false,


      level:0,
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.getQuestion();
    },
    clickNumber(num){
      var vm=this;
      if(vm.stopGame)return;
      if(!vm.$refs.InteractiveBord.timerInterval) vm.$refs.InteractiveBord.startTimer();
      if(vm.userNumber > 1000) return;

      vm.userNumber *= 10
      vm.userNumber += num;

      vm.check();
    },
    check(){
      var vm=this;
      var alert = vm.$refs.Alert;
      if(vm.userNumber.toLocaleString().length >= vm.answer.toLocaleString().length){
        if(vm.userNumber == vm.answer){
          vm.score += 2;
          alert.alertShow("+2")
          console.log("+2")
        }else {
          if(vm.score > 0)vm.score -= 1;
          if(vm.score > 0)alert.alertShow("-1","bg-red-500")
          else alert.alertShow("0","bg-red-500")
          console.log("-1")
        }

        this.userNumber = 0;
        this.getQuestion();
      }
    },
    clickClear(){
      this.userNumber = 0
    },
    getQuestion(){
      var vm=this;
      var num1=0,num2=0,questionPR,answerPR;
      var mathE = vm.mathEx[Math.floor(Math.random() * vm.mathEx.length)];

      if (mathE == "+") {
        num1 = Math.floor(Math.random() * vm.getRandomRange());
        num2 = Math.floor(Math.random() * vm.getRandomRange());
        
        questionPR = num1+" + "+num2
        answerPR = num1 + num2
      }else if (mathE == "-") {
        do {
          num1 = Math.floor(Math.random() * vm.getRandomRange());
          num2 = Math.floor(Math.random() * vm.getRandomRange());
        } while (num1 < num2);
        
        questionPR = num1+" - "+num2
        answerPR = num1 - num2
      }else if (mathE == "*") {
        num1 = Math.floor(Math.random() * (vm.getRandomRange() / 3));
        num2 = Math.floor(Math.random() * (vm.getRandomRange() / 3));
        
        questionPR = num1+" X "+num2
        answerPR = num1 * num2
      }else if (mathE == "/") {
        do {
          num1 = Math.floor(Math.random() * (vm.getRandomRange() * 2));
          num2 = Math.floor(Math.random() * (vm.getRandomRange() / 2));
        } while (num1 < num2 || num2 == 0 || num1%num2!=0);
        
        questionPR = num1+" / "+num2
        answerPR = num1 / num2
      }

      vm.question = questionPR
      vm.answer = answerPR
    },

    getRandomRange(){
      var range = 30;
      if(this.level == 0)range = 40;
      else if(this.level == 1)range = 80;
      else if(this.level == 2)range = 120;

      return range;
    },


    updateTimeOver(){
      this.stopGame = true;
      this.$refs.Alert.alertTimeOver();

    },
    updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    },

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.$refs.InteractiveBord.stopTimer(1);
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  },
}
</script>

